import React from 'react';
import { View, Text, FlatList, StyleSheet } from 'react-native';

const ResultScreen = ({ questions, score, selectedOptions }) => {
  return (
    <View style={styles.container}>
      <Text style={styles.scoreText}>Your Score: {score} / {questions.length}</Text>
      <FlatList
        data={questions}
        keyExtractor={(item, index) => index.toString()}
        renderItem={({ item, index }) => (
          <View style={styles.resultContainer}>
            <Text style={styles.questionText}>{item.question}</Text>
            <Text style={styles.correctAnswerText}>Correct Answer: {item.correctAnswer}</Text>
            <Text style={styles.selectedAnswerText}>
              Your Answer: {selectedOptions[index] || "No answer selected"}
            </Text>
          </View>
        )}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    padding: 16,
  },
  scoreText: {
    fontSize: 24,
    textAlign: 'center',
    marginVertical: 16,
  },
  resultContainer: {
    backgroundColor: '#ecf0f1',
    padding: 16,
    borderRadius: 8,
    marginVertical: 8,
  },
  questionText: {
    fontSize: 18,
    marginBottom: 8,
  },
  correctAnswerText: {
    color: 'green',
    fontSize: 18,
  },
  selectedAnswerText: {
    color: 'red',
    fontSize: 18,
  },
});

export default ResultScreen;
